@charset "UTF-8";
.z-search .z-search-match .z-search-list-content {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

/**
 * search 组件样式
 */
.z-search .z-search-match {
  position: absolute;
  top: 32px;
  left: -1px;
  z-index: 1;
  width: 170px;
  background: #fff;
  border: #707070 1px solid;
  box-sizing: border-box;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  overflow: hidden; }
  .z-search .z-search-match .z-search-list-content {
    cursor: default;
    width: 100%;
    text-align: left;
    padding: 8px;
    box-sizing: border-box; }
