  vertical-align: middle;
  width: 170px;
  position: relative;
  font-size: 16px;
  -webkit-tap-highlight-color: transparent; }
  .z-input.z-input-block {
    width: 100%; }
  .z-input.z-input-type-area > .z-input-wrap {
    border: #d6d6d6 1px solid;
    border-radius: 3px; }
    .z-input.z-input-type-area > .z-input-wrap > .z-input-wrap-border {
      border-radius: 3px;
      border: rgba(0, 0, 0, 0) 1px solid; }
  .z-input > .z-input-limit-txt {
    padding: 8px 0;
    text-align: right;
    font-size: 12px;
    color: gery-light; }
  .z-input > .z-input-wrap {
