        font: inherit;
        margin: 0;
        display: block;
        visibility: hidden;
        white-space: pre-wrap;
        word-break: break-all; }
      .z-input > .z-input-wrap .z-input-edit-box > .z-input-edit-box-input {
        border: none;
        width: 100%;
        font: inherit;
        color: inherit;
        box-sizing: border-box;
        display: block; }
        .z-input > .z-input-wrap .z-input-edit-box > .z-input-edit-box-input:focus {
          outline: none;
