/**
 * transition.scss
 */
/**
 * slide transition
 */
.z-slide-top-enter,
.z-slide-top-enter-active,
.z-slide-top-leave-active {
  transition: -webkit-transform 500ms ease-out !important;
  transition: transform 500ms ease-out !important;
  transition: transform 500ms ease-out, -webkit-transform 500ms ease-out !important; }

.z-slide-top-enter,
.z-slide-top-leave-active {
  -webkit-transform: translateY(100%) !important;
      -ms-transform: translateY(100%) !important;
          transform: translateY(100%) !important; }

.z-slide-bottom-enter,
.z-slide-bottom-enter-active,
.z-slide-bottom-leave-active {
  transition: -webkit-transform 500ms ease-out !important;
  transition: transform 500ms ease-out !important;
  transition: transform 500ms ease-out, -webkit-transform 500ms ease-out !important; }

.z-slide-bottom-enter,
.z-slide-bottom-leave-active {
  -webkit-transform: translateY(-100%) !important;
      -ms-transform: translateY(-100%) !important;
          transform: translateY(-100%) !important; }

.z-slide-left-enter,
.z-slide-left-enter-active,
.z-slide-left-leave-active {
  transition: -webkit-transform 500ms ease-out !important;
  transition: transform 500ms ease-out !important;
  transition: transform 500ms ease-out, -webkit-transform 500ms ease-out !important; }

.z-slide-left-enter,
.z-slide-left-leave-active {
  -webkit-transform: translateX(-100%) !important;
      -ms-transform: translateX(-100%) !important;
          transform: translateX(-100%) !important; }

.z-slide-right-enter,
.z-slide-right-enter-active,
.z-slide-right-leave-active {
  transition: -webkit-transform 500ms ease-out !important;
  transition: transform 500ms ease-out !important;
  transition: transform 500ms ease-out, -webkit-transform 500ms ease-out !important; }

.z-slide-right-enter,
.z-slide-right-leave-active {
  -webkit-transform: translateX(100%) !important;
      -ms-transform: translateX(100%) !important;
          transform: translateX(100%) !important; }

.z-fade-enter-active,
.z-fade-leave-active {
  opacity: 1 !important;
  transition: opacity 500ms ease-out !important; }

.z-fade-enter,
.z-fade-leave-active {
  opacity: 0 !important; }
