@charset "UTF-8";
/**
 * page 组件样式
 */
.z-page {
  color: #707070;
  text-align: center;
  -webkit-tap-highlight-color: transparent; }
  .z-page.z-page-type-more {
    display: inline-block; }
  .z-page .z-page-more {
    padding: 8px 0;
    color: #9e9e9e; }
    .z-page .z-page-more .z-page-load {
      cursor: pointer; }
  .z-page .z-page-num .z-page-length {
    color: #ccc; }
  .z-page .z-page-num .z-page-ele {
    cursor: pointer;
    min-width: 20px; }
  .z-page .z-page-num .z-page-ul {
    margin: 0 8px;
    display: inline-block;
    vertical-align: middle; }
    .z-page .z-page-num .z-page-ul > .z-page-li {
      display: inline-block;
      color: #ccc;
      padding: 4px 0;
      border-radius: 6px;
      min-width: 30px;
      text-align: center; }
      .z-page .z-page-num .z-page-ul > .z-page-li.z-page-li-active {
        background-color: #2196f3;
        color: #fff; }
      .z-page .z-page-num .z-page-ul > .z-page-li:first-child {
        margin-left: 0; }
      .z-page .z-page-num .z-page-ul > .z-page-li:last-child {
        margin-right: 0; }
  .z-page .z-page-num .z-page-total {
    color: #ccc;
    margin: 0 16px; }
  .z-page .z-page-num .z-page-search {
    display: inline-block; }
    .z-page .z-page-num .z-page-search .z-page-jump-box {
      width: 50px; }
    .z-page .z-page-num .z-page-search .z-page-jump-btn {
      margin-left: 8px; }
      .z-page .z-page-num .z-page-search .z-page-jump-btn .btn-default {
        color: #9e9e9e; }
  @media only screen and (max-width: 767px) {
    .z-page.z-page-type-more {
      width: 100%; }
    .z-page .z-page-num .z-page-search,
    .z-page .z-page-num .z-page-length {
      display: none; } }
