@charset "UTF-8";
/**
 * select-opt 组件样式
 */
.z-select-opt-ul .z-select-opt-li .z-select-opt-li-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.z-select-opt-ul {
  display: none;
  background-color: #fff; }
  .z-select-opt-ul .z-select-opt-li {
    position: relative;
    padding: 8px 16px;
    box-sizing: border-box;
    width: 100%;
    text-align: left;
    cursor: default;
    background-color: rgba(245, 245, 245, 0); }
    .z-select-opt-ul .z-select-opt-li:first-child {
      padding-top: 8px; }
    .z-select-opt-ul .z-select-opt-li:last-child {
      padding-bottom: 8px; }
    .z-select-opt-ul .z-select-opt-li.z-select-opt-default-txt {
      color: #999; }
    .z-select-opt-ul .z-select-opt-li.z-select-opt-classify-title {
      font-weight: bold; }
    .z-select-opt-ul .z-select-opt-li.z-select-opt-li-focus {
      background-color: whitesmoke; }
    .z-select-opt-ul .z-select-opt-li .z-select-opt-li-check {
      line-height: 1; }
    .z-select-opt-ul .z-select-opt-li .z-select-opt-li-text {
      display: inline-block;
      vertical-align: middle;
      width: 100%;
      box-sizing: border-box;
      line-height: normal; }

@media only screen and (max-width: 767px) {
  .z-select-opt-ul {
    width: 100%; } }
