@charset "UTF-8";
/**
 * select 组件样式
 */
.z-select > .z-select-selected-box > .z-select-init-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.z-select {
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  background-color: #fff;
  cursor: default;
  -webkit-tap-highlight-color: transparent; }
  .z-select > .z-select-read-only {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 5;
    opacity: 0; }
  .z-select > .z-select-selected-box {
    display: inline-block;
    position: relative;
    padding: 8px 30px 8px 16px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    width: 100%;
    box-sizing: border-box;
    vertical-align: middle; }
    .z-select > .z-select-selected-box > .z-select-init-text {
      width: 100%;
      outline: none;
      border: none;
      cursor: default;
      font-size: 14px; }
      .z-select > .z-select-selected-box > .z-select-init-text.z-select-default-text {
        color: #999; }
    .z-select > .z-select-selected-box > .z-select-caret-down-icon {
      position: absolute;
      right: 10px;
      top: 11px;
      height: 13px; }
  .z-select > .z-select-menu {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%; }
  .z-select .z-select-panel {
    overflow: hidden;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
    border-bottom-right-radius: 3px;
    border-bottom-left-radius: 3px;
    z-index: 2; }
    .z-select .z-select-panel .z-select-tag-opt > .z-select-ele {
      background-color: #fff;
      padding: 8px; }
    .z-select .z-select-panel .z-select-opt-comp {
      position: static;
      display: block; }
    .z-select .z-select-panel .z-select-search-input {
      box-sizing: border-box;
      box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
      background-color: #fff;
      border-bottom: 1px solid #d6d6d6;
      display: inline-block;
      width: 100%; }
  .z-select.z-select-multiple {
    position: relative;
    height: auto;
    min-height: 36px; }
    .z-select.z-select-multiple > .z-select-selected-box {
      box-sizing: border-box;
      opacity: 1; }
      .z-select.z-select-multiple > .z-select-selected-box > .z-select-init-text {
        transition-property: opacity;
        transition-duration: 150ms; }
        .z-select.z-select-multiple > .z-select-selected-box > .z-select-init-text.z-select-opacity {
          opacity: 0;
          position: absolute;
          top: 0;
          left: 0;
          height: 0;
          padding: 0; }
      .z-select.z-select-multiple > .z-select-selected-box > .z-select-scroller {
        transition: height 300ms ease;
        will-change: height; }
        .z-select.z-select-multiple > .z-select-selected-box > .z-select-scroller > .z-scroller-box > .z-select-multiple {
          margin-right: 24px; }
      .z-select.z-select-multiple > .z-select-selected-box .z-select-multiple-selected-ul > li {
        background-color: #f5f5f5;
        display: inline-block;
        margin: 4.5px 3px;
        padding: 3px; }
        .z-select.z-select-multiple > .z-select-selected-box .z-select-multiple-selected-ul > li:hover {
          background-color: #d6d6d6; }
    .z-select.z-select-multiple .z-select-panel,
    .z-select.z-select-multiple .z-select-opt-comp {
      width: 100%; }
