@charset "UTF-8";
/**
 * nav 组件样式
 */
.z-nav {
  position: relative;
  -webkit-tap-highlight-color: transparent; }
  .z-nav-arrow {
    text-align: right; }
    .z-nav-arrow-fold {
      -webkit-transform: rotate(0);
          -ms-transform: rotate(0);
              transform: rotate(0);
      transition: -webkit-transform 300ms ease-in-out;
      transition: transform 300ms ease-in-out;
      transition: transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out; }
    .z-nav-arrow-spread {
      -webkit-transform: rotate(-180deg);
          -ms-transform: rotate(-180deg);
              transform: rotate(-180deg); }
  .z-nav .z-nav-trigger {
    display: none;
    background-color: #f5f5f5;
    padding: 8px;
    border-bottom: #d6d6d6 1px solid;
    box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2); }
  .z-nav .z-nav-stage > .z-nav-motion-content > .z-nav-sub-fold {
    margin-left: 0; }
  .z-nav .z-nav-stage > .z-nav-motion-content > .z-fold > .z-fold-dl {
    cursor: pointer; }
    .z-nav .z-nav-stage > .z-nav-motion-content > .z-fold > .z-fold-dl .z-fold-dt {
      cursor: pointer;
      padding: 8px 0; }
    .z-nav .z-nav-stage > .z-nav-motion-content > .z-fold > .z-fold-dl > .z-fold-dt {
      background-color: rgba(238, 238, 238, 0);
      transition: background-color 300ms;
      padding: 12px 32px 12px 16px;
      color: rgba(0, 0, 0, 0.87);
      font-weight: bold; }
      .z-nav .z-nav-stage > .z-nav-motion-content > .z-fold > .z-fold-dl > .z-fold-dt:hover {
        background-color: rgba(238, 238, 238, 0.8); }
      .z-nav .z-nav-stage > .z-nav-motion-content > .z-fold > .z-fold-dl > .z-fold-dt .z-nav-icon {
        right: 16px; }
  .z-nav .z-nav-stage .z-nav-sub-fold {
    margin-left: 32px; }
    .z-nav .z-nav-stage .z-nav-sub-fold a {
      display: block; }
  .z-nav .z-nav-close-nav {
    display: none; }

.z-nav.z-nav-theme-success .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #4caf50; }

.z-nav.z-nav-theme-danger .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #f44336; }

.z-nav.z-nav-theme-blue .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #2196f3; }

.z-nav.z-nav-theme-warning .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #ffeb3b; }

.z-nav.z-nav-theme-orange .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #ff5722; }

.z-nav.z-nav-theme-grey .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #9e9e9e; }

.z-nav.z-nav-theme-light .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #f5f5f5; }

.z-nav.z-nav-theme-dark .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #424242; }

.z-nav.z-nav-theme-black .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #000; }

.z-nav.z-nav-theme-white .z-nav-stage .z-nav-motion-content .router-link-active {
  color: #fff; }
