@charset "UTF-8";
/**
 * nav 组件样式
 */
.z-nav.z-nav-device-s {
  font-size: 16px; }
  .z-nav.z-nav-device-s .z-nav-trigger {
    display: block;
    font-size: 21px; }
  .z-nav.z-nav-device-s .z-nav-stage {
    box-sizing: border-box;
    width: 100%;
    z-index: 999; }
    .z-nav.z-nav-device-s .z-nav-stage.z-nav-animate-slide {
      position: fixed;
      margin: auto;
      bottom: 0;
      top: 0;
      left: 0; }
      .z-nav.z-nav-device-s .z-nav-stage.z-nav-animate-slide > .z-nav-motion-content {
        background-color: #fff;
        box-sizing: border-box;
        padding: 15% 10% 10%;
        width: 80%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0; }
      .z-nav.z-nav-device-s .z-nav-stage.z-nav-animate-slide > .z-nav-motion-empty {
        background: rgba(0, 0, 0, 0.8);
        width: 20%;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0; }
    .z-nav.z-nav-device-s .z-nav-stage.z-nav-animate-fold {
      box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2);
      position: absolute;
      overflow: hidden; }
      .z-nav.z-nav-device-s .z-nav-stage.z-nav-animate-fold > .z-nav-motion-content {
        background-color: #fff; }
        .z-nav.z-nav-device-s .z-nav-stage.z-nav-animate-fold > .z-nav-motion-content > .z-nav-close-nav {
          display: none; }
        .z-nav.z-nav-device-s .z-nav-stage.z-nav-animate-fold > .z-nav-motion-content > .z-nav-sub-fold {
          padding: 24px 24px 40px; }
    .z-nav.z-nav-device-s .z-nav-stage > .z-nav-motion-content > .z-nav-close-nav {
      position: absolute;
      display: block;
      right: 16px;
      top: 16px;
      cursor: pointer; }
      .z-nav.z-nav-device-s .z-nav-stage > .z-nav-motion-content > .z-nav-close-nav .z-icon-close {
        font-size: 20px; }
    .z-nav.z-nav-device-s .z-nav-stage .z-icon-ali {
      font-size: 20px; }
    .z-nav.z-nav-device-s .z-nav-stage .z-nav-sub-fold a {
      color: rgba(0, 0, 0, 0.87);
      text-decoration: none; }
