@charset "UTF-8";
/**
 * pop 组件 bootstrap 样式
 */
.z-modal.z-modal-ui-bootstrap.z-modal-no-header .z-modal-pop > article > .z-modal-scroller > .z-scroller-box {
  padding-top: 16px; }

.z-modal.z-modal-ui-bootstrap .z-modal-pop > header {
  padding: 16px; }
  .z-modal.z-modal-ui-bootstrap .z-modal-pop > header .z-modal-header-title {
    font-size: 16px;
    line-height: 24px;
    font-weight: 500; }

.z-modal.z-modal-ui-bootstrap .z-modal-pop > article {
  padding: 16px;
  border-top: #d6d6d6 1px solid;
  border-bottom: #d6d6d6 1px solid; }

.z-modal.z-modal-ui-bootstrap .z-modal-pop > footer {
  padding: 16px; }
