@charset "UTF-8";
/**
 * btn 组件的 material UI 样式
 */
.z-btn.z-btn-ui-material:focus {
  outline: none; }

.z-btn.z-btn-ui-material .z-btn-overlay {
  background-color: rgba(0, 0, 0, 0.12);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%; }

.z-btn.z-btn-ui-material.z-btn-type-button > .z-btn-ele, .z-btn.z-btn-ui-material.z-btn-type-float > .z-btn-ele {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2); }
  .z-btn.z-btn-ui-material.z-btn-type-button > .z-btn-ele:hover, .z-btn.z-btn-ui-material.z-btn-type-float > .z-btn-ele:hover {
    box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2); }

.z-btn.z-btn-ui-material.z-btn-type-float > .z-btn-ele > .z-css-motion-rip::after {
  height: 100%;
  width: 100%;
  border-radius: 100%; }

.z-btn.z-btn-ui-material.z-btn-disabled .z-btn-ele {
  color: rgba(0, 0, 0, 0.4);
  background-color: rgba(0, 0, 0, 0.12); }

.z-btn.z-btn-ui-material.z-btn-disabled.z-btn-type-button > .z-btn-ele:hover, .z-btn.z-btn-ui-material.z-btn-disabled.z-btn-type-float > .z-btn-ele:hover {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2); }
