.z-select.z-select-ui-bootstrap {
  width: 170px;
  height: 36px;
  background-color: #fff;
  border-width: 1px;
  border-style: solid;
  border-color: #2196f3;
  border-radius: 3px;
  transition: background-color 150ms ease-out; }
  .z-select.z-select-ui-bootstrap:hover {
    border-color: rgba(0, 105, 192, 0.9); }
  .z-select.z-select-ui-bootstrap:focus {
    box-shadow: 0 0 0 4px rgba(33, 150, 243, 0.3);
    outline: none; }
  .z-select.z-select-ui-bootstrap.z-select-selecting {
    box-shadow: 0 0 0 4px rgba(33, 150, 243, 0.3);
    border-color: #0069c0; }
