@charset "UTF-8";
/*
* check 组件样式
*/
.z-check {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  -webkit-tap-highlight-color: transparent; }
  .z-check-vertical .z-check-opt-row {
    margin-left: 20px; }
  .z-check > .z-check-opt-row > .z-check-opt-col {
    display: inline-block;
    line-height: 0; }
  .z-check .z-check-overlay {
    background-color: rgba(255, 255, 255, 0);
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%; }
  .z-check .z-check-box,
  .z-check .z-check-opt-check-all {
    position: relative;
    display: inline-block; }
    .z-check .z-check-box.z-check-checked > .z-check-icon > .z-icon,
    .z-check .z-check-opt-check-all.z-check-checked > .z-check-icon > .z-icon {
      color: #2196f3; }
    .z-check .z-check-box.z-check-disabled > .z-check-lable,
    .z-check .z-check-opt-check-all.z-check-disabled > .z-check-lable {
      color: rgba(0, 0, 0, 0.38); }
    .z-check .z-check-box > .z-check-lable,
    .z-check .z-check-opt-check-all > .z-check-lable {
      display: inline-block;
      vertical-align: middle;
      line-height: 1;
      margin-left: 8px;
      cursor: default; }
  .z-check .z-check-box-style,
  .z-check .z-check-opt-check-all {
    margin: 0 20px 20px 0; }
  .z-check .z-check-icon {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    line-height: 1; }
    .z-check .z-check-icon > .z-icon {
      vertical-align: middle; }
      .z-check .z-check-icon > .z-icon .z-icon-ali {
        text-align: center; }

.z-check.z-check-theme-success .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #4caf50; }

.z-check.z-check-theme-danger .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #f44336; }

.z-check.z-check-theme-warning .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #ffeb3b; }

.z-check.z-check-theme-orange .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #ff5722; }

.z-check.z-check-theme-blue .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #2196f3; }

.z-check.z-check-theme-light .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #f5f5f5; }

.z-check.z-check-theme-dark .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #424242; }

.z-check.z-check-theme-black .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #000; }

.z-check.z-check-theme-white .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #fff; }

.z-check.z-check-theme-grey .z-check-opt-check-all.z-check-checked > .z-icon {
  color: #9e9e9e; }
