.z-select.z-select-ui-material {
  height: 36px;
  vertical-align: middle;
  border-radius: 3px; }
  .z-select.z-select-ui-material:focus {
    outline: none; }
  .z-select.z-select-ui-material.z-select-multiple {
    width: 250px;
    height: auto; }
  .z-select.z-select-ui-material.z-select-selecting {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0; }
    .z-select.z-select-ui-material.z-select-selecting .z-select-selected-box {
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0; }
      .z-select.z-select-ui-material.z-select-selecting .z-select-selected-box::after {
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
        opacity: 1; }
  .z-select.z-select-ui-material.z-select-focusing .z-select-selected-box::after {
    opacity: 1; }
  .z-select.z-select-ui-material > .z-select-selected-box {
    border-radius: 3px; }
    .z-select.z-select-ui-material > .z-select-selected-box::after {
      box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
      border-radius: 3px;
      content: '';
      opacity: 0;
      transition: opacity 300ms ease-out;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1; }
  .z-select.z-select-ui-material .z-select-opt-comp {
    border-top: none;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border: none; }
    .z-select.z-select-ui-material .z-select-opt-comp > .z-select-opt-li:first-child {
      border-top: #e5e5e5 1px solid; }
