@charset "UTF-8";
/**
 * pop 组件 material 样式
 */
.z-modal.z-modal-ui-material.z-modal-no-header .z-modal-pop > article > .z-modal-scroller > .z-scroller-box {
  padding-top: 24px; }

.z-modal.z-modal-ui-material .z-modal-pop > header {
  padding: 24px 24px 20px; }
  .z-modal.z-modal-ui-material .z-modal-pop > header .z-modal-header-title {
    font-size: 16px;
    line-height: 1; }

.z-modal.z-modal-ui-material .z-modal-pop > article > .z-modal-scroller > .z-scroller-box {
  padding: 0 24px 24px; }

.z-modal.z-modal-ui-material .z-modal-pop > footer {
  padding: 8px 8px 8px 24px; }
  .z-modal.z-modal-ui-material .z-modal-pop > footer > .z-btn {
    margin: 0 4px; }

.z-modal.z-modal-ui-material.z-modal-has-scroller .z-modal-pop > article {
  padding-top: 0;
  padding-bottom: 0; }

.z-modal.z-modal-ui-material.z-modal-has-scroller .z-modal-pop > footer {
  border-top: #d6d6d6 1px solid; }

@media only screen and (max-width: 767px) {
  .z-modal.z-modal-ui-material.z-modal-type-full .z-modal-pop > article {
    padding-top: 80px; } }
