/**
 * zenSpa/scss/config.scss
 */
.header-layout-stage {
  position: relative;
  background-color: #333333;
  height: 88px;
  padding: 0 20px; }
  .header-layout-stage > .nav-box-mobile {
    display: none; }
    .header-layout-stage > .nav-box-mobile .z-icon .z-icon-ali {
      font-size: 30px;
      color: #fff; }
    .header-layout-stage > .nav-box-mobile .z-icon .z-icon-sort {
      font-size: 40px; }
  .header-layout-stage > .nav-box {
    height: 100%;
    margin: 0 auto; }
    .header-layout-stage > .nav-box .logo-box {
      width: 30px;
      height: auto; }
    .header-layout-stage > .nav-box .nav-menu-box .z-icon .z-icon-l {
      font-size: 40px; }
    .header-layout-stage > .nav-box .nav-menu-box .router-link-active {
      color: #f5f5f5; }
      .header-layout-stage > .nav-box .nav-menu-box .router-link-active::after {
        content: " ";
        width: 100%;
        bottom: -33px;
        left: 0;
        position: absolute;
        border-bottom: #fff 5px solid; }
    .header-layout-stage > .nav-box .nav-menu-box a {
      color: #fff;
      text-decoration: none; }
    .header-layout-stage > .nav-box .nav-menu-box .z-col {
      position: relative;
      font-size: 16px;
      width: 100px;
      text-align: center; }
  .header-layout-stage > .mobile-menu {
    margin: -10px;
    display: none; }
    .header-layout-stage > .mobile-menu .menu-search > .z-input .z-input-wrap input {
      font-size: 20px; }
  @media only screen and (max-width: 575px) {
    .header-layout-stage {
      height: 60px; }
      .header-layout-stage > .nav-box {
        display: none; }
      .header-layout-stage > .nav-box-mobile {
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex; }
      .header-layout-stage > .mobile-menu {
        display: block; } }
