@charset "UTF-8";
/**
 * fold 组件样式
 */
.z-fold .z-fold-dl {
  margin: 0;
  -webkit-tap-highlight-color: transparent; }
  .z-fold .z-fold-dl > .z-fold-dt {
    position: relative;
    padding-right: 16px;
    cursor: default; }
    .z-fold .z-fold-dl > .z-fold-dt .z-fold-icon {
      position: absolute;
      height: 14px;
      margin: auto;
      right: 12px;
      top: 0;
      bottom: 0;
      -webkit-transform: rotate(0);
          -ms-transform: rotate(0);
              transform: rotate(0);
      -webkit-transform-origin: center center;
          -ms-transform-origin: center center;
              transform-origin: center center;
      transition: -webkit-transform 300ms ease-in-out;
      transition: transform 300ms ease-in-out;
      transition: transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out; }
      .z-fold .z-fold-dl > .z-fold-dt .z-fold-icon-fold {
        -webkit-transform: rotate(180deg);
            -ms-transform: rotate(180deg);
                transform: rotate(180deg); }
  .z-fold .z-fold-dl > .z-fold-dd {
    margin-left: 0;
    overflow: hidden; }
    .z-fold .z-fold-dl > .z-fold-dd > .z-fold-transition {
      will-change: height;
      transition: height 500ms ease; }

.z-fold.z-fold-ui-material .z-fold-dl > .z-fold-dt {
  padding: 4px 16px 4px 0; }
  .z-fold.z-fold-ui-material .z-fold-dl > .z-fold-dt .z-fold-icon {
    position: absolute;
    right: 8px; }
