@charset "UTF-8";
/**
 * pop 组件样式
 */
@media only screen and (max-width: 767px) {
  .z-modal .z-modal-bg {
    background: rgba(0, 0, 0, 0.5); }
  .z-modal .z-modal-pop {
    width: 96%; }
  .z-modal.z-modal-type-full .z-modal-pop {
    width: 100%;
    height: 100%;
    position: relative;
    border-radius: 0; }
    .z-modal.z-modal-type-full .z-modal-pop > header {
      position: absolute;
      top: 0;
      left: 0;
      padding-top: 12px;
      padding-bottom: 12px; }
    .z-modal.z-modal-type-full .z-modal-pop > article {
      min-height: auto;
      width: 100%;
      height: 100%;
      padding-top: 60px; } }
