@charset "UTF-8";
/**
 * code 组件样式
 */
.z-code {
  font-size: 14px;
  background-color: #fff;
  border: #6ec6ff 1px solid;
  border-radius: 3px;
  position: relative;
  padding: 8px 16px; }
  .z-code .z-code-header {
    color: #d6d6d6;
    font-weight: bold;
    text-align: right;
    margin-bottom: 5px; }
  .z-code .z-code-article .z-code-pre {
    width: 100%;
    font-family: 'Roboto Mono', Monaco, courier, monospace;
    font-size: 1em;
    line-height: 16px;
    -webkit-font-smoothing: initial;
    -moz-osx-font-smoothing: initial; }
    .z-code .z-code-article .z-code-pre .z-code-content {
      position: relative;
      padding: 0 0 0 50px; }
  .z-code .z-code-line-num {
    border-right: 2px #2196f3 solid;
    font-family: 'Roboto Mono', Monaco, courier, monospace;
    line-height: 16px;
    text-align: right;
    position: absolute;
    top: 0;
    left: 0;
    padding-right: 5px;
    width: 24px; }

