@charset "UTF-8";
/**
 * list 组件样式
 */
.z-list {
  position: relative;
  background-color: #fff;
  overflow: hidden; }
  .z-list .z-list-page {
    background-color: #fff;
    margin: 0 auto;
    width: 100%; }
  .z-list .z-list-ul {
    list-style-type: none; }
    .z-list .z-list-ul > .z-list-li:last-child {
      border: none; }
  .z-list .z-list-empty-data {
    padding: 8px 0;
    text-align: center; }
