/**
 * zenSpa/scss/config.scss
 */
html, body {
  color: #666666;
  font-size: 14px;
  font-weight: normal;
  font-family: "Microsoft Yahei" ,"simSun", "Arial";
  margin: 0; }

ul {
  margin: 0;
  padding: 0; }

a:link, a:visited, a:hover, a:active {
  color: #0099FF; }

a:link {
  text-decoration: none; }

a:hover {
  text-decoration: underline; }
