@charset "UTF-8";
/**
 * table 组件样式
 */
.z-table {
  position: relative; }
  .z-table.z-table-stripe .z-table-header-group {
    background: #f5f5f5; }
  .z-table.z-table-stripe .z-table-row:hover {
    background-color: transparent; }
  .z-table.z-table-stripe .z-table-row:nth-child(2n) {
    background: #f5f5f5; }
  .z-table-page {
    border-top: #d6d6d6 1px solid; }
  .z-table .z-table-row:hover {
    background-color: #f5f5f5; }
  .z-table .z-table-col {
    text-align: left; }
  .z-table .z-table-col > div {
    display: inline-block; }
  .z-table.z-table-border-row .z-table-header-group .z-table-row, .z-table.z-table-border-all .z-table-header-group .z-table-row {
    border-bottom: #d6d6d6 1px solid; }
  .z-table.z-table-border-row .z-table-row-group .z-table-row, .z-table.z-table-border-all .z-table-row-group .z-table-row {
    border-bottom: #d6d6d6 1px solid; }
    .z-table.z-table-border-row .z-table-row-group .z-table-row:last-child, .z-table.z-table-border-all .z-table-row-group .z-table-row:last-child {
      border-bottom: none; }
  .z-table.z-table-border-col .z-table-row .z-table-col, .z-table.z-table-border-all .z-table-row .z-table-col {
    border-right: #d6d6d6 1px solid; }
    .z-table.z-table-border-col .z-table-row .z-table-col:last-child, .z-table.z-table-border-all .z-table-row .z-table-col:last-child {
      border-right: none; }
  .z-table .z-table-empty-data {
    color: #f44336;
    text-align: center;
    padding: 8px 0; }
  .z-table .z-table-wrap {
    background: #fff;
    border-collapse: collapse;
    width: 100%; }
  .z-table .z-table-header {
    padding: 16px 24px; }
  .z-table .z-table-col {
    padding: 16px 24px; }
