@charset "UTF-8";
/**
 * loading 组件的 bootstrap UI 样式
 */
@-webkit-keyframes z-loading-rotate {
  0% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg); }
  50% {
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg); }
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }
@keyframes z-loading-rotate {
  0% {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg); }
  50% {
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg); }
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); } }

.z-loading.z-loading-ui-bootstrap .z-loading-rotate .z-loading-icon {
  line-height: 0;
  -webkit-transform-origin: 50%;
      -ms-transform-origin: 50%;
          transform-origin: 50%;
  -webkit-animation: z-loading-rotate 1s linear infinite;
          animation: z-loading-rotate 1s linear infinite;
  margin: 0; }

@-webkit-keyframes z-loading-spot-fade-1 {
