/**
 * scroller 组件样式
 */
.z-scroller {
  overflow: hidden;
  position: relative; }
  .z-scroller .z-scroller-box {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translateX(0) translateY(0);
        -ms-transform: translateX(0) translateY(0);
            transform: translateX(0) translateY(0); }
  .z-scroller .z-scroller-bar {
    position: absolute;
    border-radius: 4px;
    background-color: #9e9e9e;
    opacity: .6;
    transition: opacity 150ms ease-out;
    z-index: 1; }
    .z-scroller .z-scroller-bar:hover {
      opacity: .8; }
    .z-scroller .z-scroller-bar.z-scroller-x-bar {
      bottom: 0;
      left: 0;
      height: 5px; }
    .z-scroller .z-scroller-bar.z-scroller-y-bar {
      right: 0;
      top: 0;
      width: 5px; }
