@charset "UTF-8";
/*
* icon 组件样式
*/
.z-icon {
  display: inline-block;
  line-height: 0;
  color: #2196f3;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .z-icon-stage {
    display: inline-block;
    line-height: 0; }
  .z-icon-size-xxs {
    font-size: 12px; }
  .z-icon-size-xs {
    font-size: 14px; }
  .z-icon-size-s {
    font-size: 20px; }
  .z-icon-size-m {
    font-size: 30px; }
  .z-icon-size-l {
    font-size: 40px; }
  .z-icon-size-xl {
    font-size: 50px; }
  .z-icon-ali {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden; }

.z-icon.z-icon-theme-success {
  color: #4caf50; }

.z-icon.z-icon-theme-danger {
  color: #f44336; }

.z-icon.z-icon-theme-warning {
  color: #ffeb3b; }

.z-icon.z-icon-theme-blue {
  color: #2196f3; }

.z-icon.z-icon-theme-orange {
  color: #ff5722; }

.z-icon.z-icon-theme-light {
  color: #f5f5f5; }

.z-icon.z-icon-theme-dark {
  color: #424242; }

.z-icon.z-icon-theme-black {
  color: #000; }

.z-icon.z-icon-theme-white {
  color: #fff; }

.z-icon.z-icon-theme-grey {
  color: #9e9e9e; }
